body {
  font-family: "Open Sans", sans-serif;
  width: 100vw;
}

.container {
  width: 100vw;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  z-index: 1;
}

.header {
  height: 70px;
  box-shadow: 0 3px 6px #00000029;
  background: #fff;
  position: fixed;
  width: 100vw;
  z-index: 999999;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.header .container #hamburger {
  display: none;
}
@media (max-width:768px) {
  .header .container #hamburger {
    display: block;
    font-size: 1.5em;
    padding-right: 1.5em;
    color: #08A6E4;
  }
}
.header .container .logo {
  font-family: "Raleway", sans-serif;
  color: #08A6E4;
  font-size: 32px;
  font-weight: 300;
  text-decoration: none;
}
.header .container #menu .menu__list {
  display: flex;
  justify-content: space-around;
  list-style: none;
}
@media (max-width:768px) {
  .header .container #menu .menu__list {
    display: flex;
    flex-direction: column;
    justify-content: left;
  }
}
.header .container #menu .menu__list .menu__el {
  padding-left: 47px;
  padding-right: 47px;
}
@media (max-width:768px) {
  .header .container #menu .menu__list .menu__el {
    padding: 0;
  }
}
.header .container #menu .menu__list .menu__el .menu__link {
  text-transform: uppercase;
  text-decoration: none;
  color: #A5A5A5;
  font-size: 11px;
  font-weight: 700;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  transition: 0.5s;
  padding-top: 28px;
  padding-bottom: 28px;
}
.header .container #menu .menu__list .menu__el .menu__link:hover {
  color: #000;
  border-bottom: 4px solid #08A6E4;
}
@media (max-width:768px) {
  .header .container #menu {
    display: none;
    transition: 1s;
  }
}
.header .container #menu-visibility {
  display: none;
}
.header .container #menu-visibility:checked + #menu {
  display: flex;
  justify-content: left;
  width: 100vw;
  padding-top: 20em;
  background: #fff;
  box-shadow: 0 3px 6px #00000029;
}

.hero {
  height: 100vh;
  min-height: 712px;
  background-size: contain;
  background: url("../../images/Background.png") no-repeat bottom;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (max-width:768px) {
  .hero {
    background-size: 200%;
    width: 100%;
  }
}
.hero .hero__title {
  margin-top: -300px;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-size: 49px;
  text-transform: capitalize;
}
@media (max-width:768px) {
  .hero .hero__title {
    margin-top: 0;
  }
}
.hero .hero__text {
  font-size: 16px;
  text-align: center;
  padding-bottom: 40px;
}
.hero .hero__button {
  background: #08A6E4;
  border-radius: 40px;
  padding: 22px 157px;
  box-shadow: 5px 10px 14px #07ACE629;
  transition: 0.5s;
}
@media (max-width:768px) {
  .hero .hero__button {
    padding: 1.5rem 8rem;
  }
}
.hero .hero__button .hero__btn {
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  transition: 0.5s;
}
.hero .hero__button:hover,
.hero .hero__btn:hover {
  background: grey;
}

.stats {
  height: 530px;
}
@media (max-width:768px) {
  .stats {
    height: 100%;
  }
}
@media (max-width:768px) {
  .stats .stats__container {
    padding-right: 1.5rem;
  }
}
.stats .stats__container .stats__title {
  font-family: "Raleway", sans-serif;
  font-size: 36px;
  font-weight: 300;
  display: flex;
  justify-content: center;
  padding-top: 100px;
  text-align: center;
}
@media (max-width:768px) {
  .stats .stats__container .stats__title {
    padding-top: 0.5rem;
  }
}
.stats .stats__container .stats__content {
  margin-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.stats .stats__container .stats__content .stats__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 80px;
  margin-left: 80px;
}
.stats .stats__container .stats__content .stats__info .stats__info-icons {
  display: flex;
  margin-left: -20px;
}
.stats .stats__container .stats__content .stats__info .stats__info-icons .stats__info-image {
  margin: -30px;
  z-index: 99;
}
.stats .stats__container .stats__content .stats__info .stats__info-icons .stats__info-circle {
  height: 60px;
  width: 60px;
  background: #FB3B64;
  border-radius: 30px;
}
.stats .stats__container .stats__content .stats__info .stats__info-icons .stats__info-circle2 {
  height: 60px;
  width: 60px;
  background: #08A6E4;
  border-radius: 30px;
}
.stats .stats__container .stats__content .stats__info .stats__info-icons .stats__info-circle3 {
  height: 60px;
  width: 60px;
  background: #55DFB4;
  border-radius: 30px;
}
.stats .stats__container .stats__content .stats__info .stats__info-number {
  font-family: "Raleway", sans-serif;
  font-size: 20px;
  font-weight: 800;
  letter-spacing: 6px;
}
.stats .stats__container .stats__content .stats__info .stats__info-text {
  font-family: "Raleway", sans-serif;
  font-size: 17px;
  font-weight: 300;
  color: #A5A5A5;
}

.benefits {
  background: #F7F7F7;
}
.benefits .benefits__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0;
}
.benefits .benefits__container .benefits__window {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 30%;
  background: #fff;
  margin: 20px;
  border: 1px solid #E8E9ED;
  border-radius: 5px;
  width: 90%;
}
@media (max-width:768px) {
  .benefits .benefits__container .benefits__window {
    flex-wrap: wrap;
    flex-direction: column-reverse;
    justify-content: left;
    align-items: flex-start;
  }
}
.benefits .benefits__container .benefits__window .window__info {
  padding: 35px;
  max-width: 416px;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width:768px) {
  .benefits .benefits__container .benefits__window .window__info {
    max-width: 100%;
  }
}
.benefits .benefits__container .benefits__window .window__info .window__title {
  font-family: "Raleway", sans-serif;
  font-size: 36px;
  font-weight: 400;
}
.benefits .benefits__container .benefits__window .window__info .window__text {
  font-size: 16px;
  font-weight: 400;
  padding-bottom: 50px;
  border-bottom: 4px solid #FB3B64;
}
@media (max-width:768px) {
  .benefits .benefits__container .benefits__window .window__info .window__text {
    border: none;
    padding-bottom: 20px;
  }
}
.benefits .benefits__container .benefits__window:first-child {
  margin-top: 105px;
}
.benefits .benefits__container .benefits__window:last-child {
  margin-bottom: 105px;
}
.benefits .benefits__container .benefits__window .window__photo {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.benefits .benefits__container .benefits__window .window__photo .window__photo-square {
  background: #FB3B64;
  border-radius: 40px;
  height: 150px;
  width: 150px;
  transform: rotate(45deg);
  position: absolute;
  margin-left: 30%;
  margin-bottom: 10%;
}
@media (max-width:768px) {
  .benefits .benefits__container .benefits__window .window__photo .window__photo-square {
    height: 7rem;
    width: 7rem;
    margin-left: 40%;
    margin-bottom: 10%;
  }
}
.benefits .benefits__container .benefits__window .window__photo .window__photo-image {
  max-width: 45%;
  height: auto;
  z-index: 999;
  display: flex;
  justify-content: center;
}
@media (max-width:768px) {
  .benefits .benefits__container .benefits__window .window__photo .window__photo-image {
    padding-top: 6rem;
    max-width: 70%;
  }
}
