.pricing .pricing__container .pricing__title {
  font-family: "Raleway", sans-serif;
  font-size: 36px;
  font-weight: 400;
  text-align: center;
}
.pricing .pricing__container .pricing__content {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@media (max-width:768px) {
  .pricing .pricing__container .pricing__content {
    flex-direction: column;
  }
}
.pricing .pricing__container .pricing__content .pricing__option {
  border: 1px solid #FB3B64;
  border-radius: 18px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 460px;
  width: 370px;
  position: relative;
  transition: 1s;
}
@media (max-width:768px) {
  .pricing .pricing__container .pricing__content .pricing__option {
    margin-bottom: 4rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    height: 20rem;
  }
}
.pricing .pricing__container .pricing__content .pricing__option .pricing__text .option__title {
  font-family: "Raleway", sans-serif;
  font-size: 21px;
  font-weight: 600;
  text-align: center;
}
@media (max-width:768px) {
  .pricing .pricing__container .pricing__content .pricing__option .pricing__text .option__title {
    font-size: 2em;
  }
}
.pricing .pricing__container .pricing__content .pricing__option .pricing__text .option__price {
  font-size: 36px;
  font-weight: 300;
  color: #FB3B64;
  margin-top: 0;
  text-align: center;
}
@media (max-width:768px) {
  .pricing .pricing__container .pricing__content .pricing__option .pricing__text .option__price {
    font-size: 3em;
  }
}
.pricing .pricing__container .pricing__content .pricing__option .pricing__list .option__info .option__info-yes {
  display: flex;
}
.pricing .pricing__container .pricing__content .pricing__option .pricing__btn {
  width: 150px;
  height: 50px;
  border-radius: 25px;
  background-color: #FB3B64;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 50%);
  text-align: center;
  text-decoration: none;
  color: white;
  font-weight: 600;
  padding-top: 15px;
  transition: 1s;
}
.pricing .pricing__container .pricing__content .pricing__option .pricing__btn:hover {
  box-shadow: 0 0 27px 2px #FB3B64;
}
